var stream = weex.requireModule('stream');
var navigator = weex.requireModule('navigator')
var modal = weex.requireModule('modal')
let utils = {
    // 发送请求
    WeexAjax(obj) {
        let me = this;
        let URL = obj.url;
        let Result = "loding...";
        if (obj.method == 'POST' || obj.method == 'GET') {
            stream.fetch({
                headers: { 'Content-Type': 'application/x-www-form-urlencoded'/*,'Authorization': 'Bearer ' + me.gets('user_token')*/},
                method: obj.method,
                url: "http://47.92.164.211:8011/" + URL,
                type: obj.type,
                // body: 'MobilePhone='+this.loginValue.name
                // 拼接字符串
                body: me.toParams(obj.body)
            }, function(res) {
                if (!res.ok) {
                    me.Result = "request failed";
                    // me.loginTip = '请求错误,请重试!'
                } else {
                    // 返回相应内容
                    obj.callback(res.data);
                }
            }, function(response) {});
        } else {
            console.log('请求方式错误')
        }
    },
    // body请求主体转换函数
    toParams(obj) {
        var param = ""
        for (const name in obj) {
            if (typeof obj[name] != 'function') {
                param += "&" + name + "=" + encodeURI(obj[name])
            }
        }
        return param.substring(1)
    },

    // 页面跳转navigation
    jump(href, event) {
        var bundleUrl = this.bundleUrl;
        var url = decodeURI(weex.config.bundleUrl); //取得整个地址栏
        var result = url.match(new RegExp("[a-zA-z]+://[^\s]{19}", "g"));
        if (WXEnvironment.platform === 'Web') {
            console.warn('Web端跳转待开发')
            // window.location.href = 'http://192.168.1.103:8082/src/components/other/find.vue'
            window.location.href = href
        } else {
            navigator.push({
                // url: '../../../dist/components/other/find.js',
                url: result + 'dist/' + href,
                // url: 'http://192.168.1.104:8082/dist/components/other/find.js',
                animated: "true"
            }, event => {
                // modal.toast({ message: 'callback: ' + event })
            })
        }
    },
    // 判断当前处于哪个设备
    device() {
        if (WXEnvironment.platform === 'Android') {
            console.log('Android')
            return 0;
        } else if (WXEnvironment.platform === 'iOS') {
            console.log('iOS')
            return 1;
        } else {
            console.log('Web')
            return 2;
        }
    },
    // 改变this指向问题
    bindThis(f, oTarget) {
        return function() {
            return f.apply(oTarget, arguments);
        };
    },
    sets(obj) {
        for (var item in obj) {
            console.log(obj[item]);
            localStorage.setItem(item, obj[item]);
        }
    },
    gets(item) {
        return localStorage.getItem(item);
    }
}
export default utils;


