<template>
	<div class="class">
		<sHeader></sHeader>
		<div class="class-box">
			<div class="left-box">
				<!-- 左侧导航 -->
				<list class="nav-list" show-scrollbar="false">
				    <cell class="cell" v-for="(num,index) in classArr" @click="select(index)" :key="index">  
				      	<div :class="[navIndex === index ? 'panel-crt' : 'panel']">
				        	<text :class="[navIndex === index ? 'text-crt' : 'text']">{{num.CategoryName}}</text><!-- CategoryName -->
				      	</div>
				    </cell>
				</list>
			</div>
			<div class="right-box">
				<div class="sec-nav">
					<!-- 右侧头部导航 -->
					<scroller class="sec-nav-list" show-scrollbar="false" scroll-direction="horizontal">
					    <div class="cells" v-for="(nums,indexs) in pList.sNav" @click="selects(indexs)" :key="indexs">
					      	<text :class="[navIndexs === indexs ? 'texts-crt' : 'texts']">{{nums.title}}</text>
					    </div>
					</scroller>
					<div class="slideShow">
						<text class="iconFont slideShow-icon" @click="openCover" ref="slideBtn">&#xe731;</text>
					</div>
				</div>
				<div class="product-box">
					<!-- 右侧下面内容 -->
					<scroller class="product-list">
					    <div class="cellp" v-for="(parent,index) in classArr" :key="index" ref="item">
				        	<text class="nav-title">{{parent.title}}</text>
				        	<list>
					        	<cell class="cellps" v-for="child,cindex in parent.product" :key="child.index">
					        		<image class="product-img" :src="child.urls"></image>
					        		<div class="pro-news">
					        			<text class="product-title">{{child.name}}</text>
					        			<div class="product-type-box"><text :class="[child.sellOut === true ? 'product-type' : 'product-types']">已售完</text></div>
					        			<div class="pro-m">
					        				<text class="product-price">￥{{child.price}}</text>
					        				<text class="product-prices">￥{{child.prices}}</text>
					        				<image class="shop-car-icon" src="../src/components/class/shop-car-icon.png"></image>
					        			</div>
					        		</div>
					        	</cell>
				        	</list>
					    </div>
					</scroller>
					<!--<div class="nav-head"><text class="nav-title">火锅类</text></div>-->
				</div>
			</div>
		</div>
		<div ref="slides" class="cover"  @click="closeCover">
			<div class="demo-content">
	        	<div class="slide-list">
					<div class="cells slide-list-cell" v-for="(nums,indexs) in pList.sNav" @click="selects(indexs)" :key="indexs">
						<text :class="[navIndexs === indexs ? 'texts-crt' : 'texts']">{{nums.title}}</text>
					</div>
				</div>
	      	</div>
		</div>
	</div>
</template>

<script>
	import Utils from '../../common/utils/utils.js'
    //1.先使用import导入你要在该组件中使用的子组件
    import sHeader from '../header/searchHeader.vue'
    import { WxcPopup } from 'weex-ui'
    const dom = weex.requireModule('dom')
    const animation = weex.requireModule('animation')
  	const modal = weex.requireModule('modal')
  	const CLASS_URL = 'api/product/getcagegorylist'
    export default {
    	data () {
    		return{
				list:[
					{
						title:'速食',
						id:1,
						urls:"../src/json/sushi.json"
					},{
						title:'冲调类',
						id:2,
						urls:"../src/json/chongtiao.json"
					},{                      
						title:'熟食',
						id:3,
						urls:"../src/json/shushi.json"
					}
				],
				pList:{
					sNav:[
					    {
							title:"火锅类",
							id:1,
							product:[
								{
									name:"猪肉卷1猪肉卷1猪肉卷1猪肉卷1",
									price:"10.90/盒",
									prices:"12.00",
									urls:"../src/components/class/product-img01.png",
									newProduct:false,
									sellOut:true
								},{
									name:"猪肉卷1",
									price:"10.90/盒",
									prices:"12.00",
									urls:"../src/components/class/product-img01.png",
									newProduct:false,
									sellOut:false
								},{
									name:"猪肉卷1",
									price:"10.90/盒",
									prices:"12.00",
									urls:"../src/components/class/product-img01.png",
									newProduct:false,
									sellOut:true
								}
							]
						},{
							title:"速冻面食",
							id:2,
							product:[
								{
									name:"猪肉馄饨1",
									price:"10.90/盒",
									prices:"12.00",
									urls:"../src/components/class/product-img01.png",
									newProduct:false,
									sellOut:true
								},{
									name:"猪肉馄饨1",
									price:"10.90/盒",
									prices:"12.00",
									urls:"../src/components/class/product-img01.png",
									newProduct:false,
									sellOut:true
								},{
									name:"猪肉馄饨1",
									price:"10.90/盒",
									prices:"12.00",
									urls:"../src/components/class/product-img01.png",
									newProduct:false,
									sellOut:true
								}
							]
						}
					],
				},
				//分类数组
				classArr: {},
				navIndex: 0,
				navIndexs:0,
				bool:true
			}
		},
        //2.然后,在components中写入子组件
        components: {sHeader,WxcPopup},
        methods: {
        	select(index){
        		var stream = weex.requireModule('stream');
        		var urls = this.list[index].urls;
        		var self = this;
        		this.navIndex = index;
        		//为了在内部函数能使用外部函数的this对象，要给它赋值了一个名叫self的变量。
	            // stream.fetch({
	            //     url: urls,
	            //     method: 'get',
	            //     dataType: 'jsonp'
	            // },(res) =>{
	            // 	self.pList = JSON.parse(res.data);
	            // 	//console.log(self.pList);
	            // 	//console.log(res);
	            // },() => {})
        	},
        	selects(indexs){
        		console.log(indexs);
        		var testEl = this.$refs.slides;
        		var testEt = this.$refs.slideBtn;
        		//const item = "item" + indexs;
        		//console.log(item);
        		this.navIndexs = indexs;
        		const el = this.$refs.item[indexs];
        		console.log(this.$refs.item[indexs].clientHeight);
		        dom.scrollToElement(el, {});
		        animation.transition(testEl, {
		          styles: {
		            transform: 'translateX(0)',
		          },
		          duration: 500, //ms
		          timingFunction: 'ease',
		          delay: 0 //ms
		        });
		        animation.transition(testEt, {
		          styles: {
		            transform: 'rotateX(0deg)',
		          },
		          duration: 500, //ms
		          timingFunction: 'ease',
		          delay: 0 //ms
		        });
		        this.bool = true;
        	},
        	openCover() {
		        var testEl = this.$refs.slides;
		        var testEt = this.$refs.slideBtn;
		        if (this.bool) {
		        	animation.transition(testEl, {
			          styles: {
			            transform:  'translateX(-600px)',
			          },
			          duration: 500, //ms
			          timingFunction: 'ease',
			          delay: 0 //ms
			        });
			        animation.transition(testEt, {
			          styles: {
			            transform:  'rotateX(180deg)',
			          },
			          duration: 500, //ms
			          timingFunction: 'ease',
			          delay: 0 //ms
			        });
			        this.bool = false;
		        } else{
		        	animation.transition(testEl, {
			          styles: {
			            transform: 'translateX(0)',
			          },
			          duration: 500, //ms
			          timingFunction: 'ease',
			          delay: 0 //ms
			        });
			        animation.transition(testEt, {
			          styles: {
			            transform:  'rotateX(0deg)',
			          },
			          duration: 500, //ms
			          timingFunction: 'ease',
			          delay: 0 //ms
			        });
			        this.bool = true;
		        }
		    },
		    //非状态组件，需要在这里关闭
		    closeCover() {
		        var testEl = this.$refs.slides;
		        var testEt = this.$refs.slideBtn;
		        animation.transition(testEl, {
		          styles: {
		            transform: 'translateX(0)',
		          },
		          duration: 500, //ms
		          timingFunction: 'ease',
		          delay: 0 //ms
		        });
		        animation.transition(testEt, {
		          styles: {
		            transform:  'rotateX(0deg)',
		          },
		          duration: 500, //ms
		          timingFunction: 'ease',
		          delay: 0 //ms
		        });
		        this.bool = true;
		    }
        }, 
        created() {
        	var _this = this;
        	Utils.WeexAjax({
        		url: 'api/product/getcagegorylist',
	    		method: 'GET',
	    		type: 'JSON',
	    		callback: function(ret){
	    			var rets = JSON.parse(ret);
	    			console.log(rets)
	    			if(rets.Status == 1){
	    				_this.classArr = JSON.parse(ret).obj;
	    			}
	    		}
        	})
	        var fontModule = weex.requireModule("dom");
	        fontModule.addRule('fontFace', {
	            'fontFamily': "iconfont",
	            'src': "url('//at.alicdn.com/t/font_948634_gubgm8w1dr.ttf')"
        	});
    	}
    }
</script>

<style lang="stylus" scoped>
	.left-nav{
		width: 150px;
	}
	.nav-list{
		width: 150px;
		position: fixed;
		top: 90px;
		left: 0;
		bottom: 110px;
		background-color: rgba(246,246,246,1);
	}
	.panel {
	    height: 88px;
	    padding-top: 16px;
	    padding-bottom: 16px;
	}
	.panel-crt {
	    height: 88px;
	    padding-top: 16px;
	    padding-bottom: 16px;
	    background-color: #fff;
	}
  	.text {
    	font-size: 24px;
    	line-height: 56px;
    	text-align: center;
    	color: #333;
    	border-left-width: 8px;
	    border-left-style: solid;
	    border-left-color: rgba(115,204,0,0);
  	}
  	.text-crt {
    	font-size: 24px;
    	line-height: 56px;
    	text-align: center;
    	color: #41B883;
    	border-left-width: 8px;
	    border-left-style: solid;
	    border-left-color: rgba(115,204,0,1);
  	}
  	.right-box{
  		width: 580px;
  		position: fixed;
  		top: 90px;
  		bottom: 110px;
  		right: 0;
  	}
  	.iconFont{
	    font-family: iconfont;
	}
  	.sec-nav{
  		flex-direction: row;
  	}
  	.sec-nav-list{
  		flex: 1;
  		flex-direction: row;
  		height: 88px;
  		padding-top: 24px;
  		border-bottom-width: 1px;
  		border-bottom-color: #d2d2d2;
  		border-bottom-style: solid;
  		margin-right: 20px;
  	}
  	.slideShow{
  		width: 80px;
  		height: 88px;
  		position: absolute;
  		top: 0;
  		right: 0;
  	}
  	.slideShow-icon{
  		font-size: 50px;
  		color: #999;
  		line-height: 88px;
  		text-align: center;
  	}
  	.cover{
  		width: 600px;
  		position: fixed;
  		top: 177px;
  		bottom: 110px;
  		right: -600px;
  		background-color: rgba(0,0,0,0.5);
  		transition: all .5s ease;
  		overflow: hidden;
  	}
  	.demo-content{
  		background-color: #fff;
  	}
  	.cells{
  		width: auto;
  		height: 40px;
  		padding-left: 20px;
  		padding-right: 20px;
  		border-width: 1px;
  		border-color: #d2d2d2;
  		border-style: solid;
  		margin-left: 15px;
  		border-radius: 40px;
  	}
  	.slide-list{
  		flex-direction: row;	
  		padding-top: 10px;
  		padding-bottom: 10px;
  	}
  	.slide-list-cell{
  		width: 173px;
  		margin-left: 20px;
  		margin-top: 10px;
  		margin-bottom: 10px;
  	}
  	.texts{
  		font-size: 22px;
  		line-height: 38px;
  		text-align: center;
  	}
  	.texts-crt{
  		font-size: 22px;
  		line-height: 38px;
  		color: #41B883;
  		text-align: center;
  	}
  	.product-box{
  		width: 580px;
  		position: fixed;
  		top: 178px;
  		bottom: 110px;
  		right: 0;
  	}
  	.nav-head{
  		width: 580px;
  		position: absolute;
  		top: 0;
  		left: 0;
  		background-color: #fff;
  		padding-right: 20px;
  	}
  	.nav-title{
  		width: 560px;
  		padding-top: 15px;
  		padding-bottom: 15px;
  		font-size: 22px;
  		border-bottom-width: 1px;
  		border-bottom-color: #d2d2d2;
  		border-bottom-style: solid;
  	}
  	.cellps{
  		flex-direction: row;
  		border-bottom-width: 1px;
  		border-bottom-color: #d2d2d2;
  		border-bottom-style: solid;
  		padding-top: 30px;
  		padding-bottom: 30px;
  		margin-right: 20px;
  	}
  	
  	.product-img{
  		width: 200px;
  		height: 200px;
  		margin-right: 30px;
  	}
  	.pro-news{
  		flex: 1;
  	}
  	.product-title{
  		font-size: 30px;
  		color: #333;
  		margin-top: 20px;
  		text-overflow: ellipsis;
  		lines:1; 
  	}
  	.product-type-box{
  		height: 36px;
  		margin-top: 10px;
  	}
  	.product-type{
  		width: 80px;
  		font-size: 20px;
  		line-height: 36px;
  		color: #fff;
  		text-align: center;
  		border-radius: 5px;
  		background-color: #ccc;
  	}
  	.product-types{
  		opacity: 0;
  	}
  	.pro-m{
  		height: 42px;
  		flex-direction: row;
  		align-items: flex-end;
  	}
  	.product-price{
  		font-size: 30px;
  		font-weight: bold;
  		color: #f26100;
  		margin-right: 10px;
  	}
  	.product-prices{
  		font-size: 20px;
  		color: #777;
  	}
  	.shop-car-icon{
  		width: 42px;
  		height: 42px;
  		position: absolute;
  		bottom: 0;
  		right: 0;
  	}
</style>


